@media print {
    #print {
        font-size: 100%;
        margin-bottom: 0;

        .phpdebugbar {
            display: none;
        }

        #app {
            &.py-4 {
                padding-top: 0 !important;
                padding-bottom: 0 !important;
            }

            height: 100vh;
        }

        .navbar,
        .print {
            display: none;
        }

        .page {
            width: 100% !important;
            max-width: 100% !important;
        }
    }
}

#print {
    #app {
        display: flex;
        justify-content: center;
        flex-direction: column;
        align-items: center;
        padding: 0 !important;
    }

    .page {
        min-width: 21cm;
        max-width: 21cm;
        height: 100%;
        min-height: 29.7cm;
        background-color: white;
        box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        padding-top: 2cm;

        .container {
            padding: 0 2cm;
        }
    }

